import React, { Component } from 'react';
import FbImageLibrary from './react-fb-image-grid/lib/components/Images'
import './../../App.css';
import { Skeleton, Switch, Card, Icon, Avatar } from 'antd';

const { Meta } = Card;

class Post extends Component {
  
  static defaultProps = {
    name:'Muhammad Hoti',
    dp:'https://scontent.fkhi2-1.fna.fbcdn.net/v/t1.0-9/35550913_1813997585290118_6452855055597961216_n.jpg?_nc_cat=107&oh=42d7d8c46cfe353c5c2271b1d7f0e2b0&oe=5C1E0222',
    description:'Waiting For Game Of Thrones Season 7',
    imgObj:
      ['https://images5.alphacoders.com/637/thumb-1920-637668.jpg','https://i.imgur.com/G2Jsw4o.jpg']
    
  }

  constructor(props){
    super(props)
    this.state={
      loading: false,
    }
  }

  render() {
    const {name,dp,description,imgObj} = this.props
    const {loading} = this.state;
    return (
      <div className="main">
        <Card className="card"
          style={{ width: 500, marginTop: 16 }}
        >
          <Skeleton loading={loading} avatar active>
            <Meta
              avatar={<Avatar src={dp} />}
              title={name}
              description={description}
            />
            
          </Skeleton>
        </Card>
        <div className="fbGrid">
          <FbImageLibrary width={51} align={'center'} hideOverlay={false} overlayBackgroundColor={'green'} countFrom={2}  images={imgObj}/>
        </div>
        <div className="fbButtons">
        <p style={{display:"inline", border:"solid 1px black",padding: 5,borderRadius: 10}}>
          Like
            <img src='data:image/png;base64,' />
          </p>
          <p style={{display:"inline", border:"solid 1px black",padding: 5,borderRadius: 10}}>
          Comment
            <img src='data:image/png;base64,' />
          </p>
          <p style={{backgroundColor:'#fff', display:"inline", border:"solid 1px black",padding: 5,borderRadius: 10,}}>
          Share
            <img src='data:image/png;base64,' />
          </p>
        </div>
        
        
      </div>
    );
  }
}

export default Post;